import React from "react"
import ReactDOM from "react-dom"
import "./index.css"
import App from "./ui/App"
import {Provider} from "react-redux"
import {store} from "./bll/redux-store"

ReactDOM.render(
    <React.StrictMode>
        <Provider store={store}>
            <App/>
        </Provider>
    </React.StrictMode>,
    document.getElementById("root")
)

